.userInfo{
    display: flex;
    margin-left: 15px;
    flex-direction: column;
    justify-content: space-between;
}
.userName{
    font-weight: 600;
    font-size: 18px;

}
.userStatus{
    font-size: 15px;
    color: rgba(0,0,0,0.6);
    margin-bottom: 40px;
}
.userLocation {
    margin-left: auto;
}
.userLocation span{
    display: block;
}
.selectedPage{
    font-weight: 600;
}
.page{
    padding: 0px 5px;
    cursor: pointer;
}
